import React, { Component } from 'react';
import axios from 'axios';

import GameCard from './GameCard';

export default class AppContent extends Component {
  constructor() {
    super();

    this.state = {
      competition: '',
      matchDay: 0,
      date: new Date(),
      games: []
    }
  }

  componentDidMount() {
    this.getFixtures();
  }

  async setMatchDay() {
    try {
      const result = await axios.get('https://api.football-data.org/v1/competitions/467', {
        headers: {
          'X-Auth-Token': '81d877eff8154f66af1dc6d06b713a52'
        }
      });
      this.setState({
        competition: result.data.caption,
        matchDay: result.data.currentMatchday
      })
    } catch (err) {
      console.log('-----===== API COMPETITIONS =====-----');
      console.error(err)
    };
  }

  async getEventsByMatchDay() {
    try {
      const result = await axios.get(`https://api.football-data.org/v1/competitions/467/fixtures/?matchday=${this.state.matchDay}`, {
        headers: {
          'X-Auth-Token': '81d877eff8154f66af1dc6d06b713a52'
        }
      })

      this.setState({
        games: result.data.fixtures
      });
    } catch(err) {
      console.log('-----===== API FIXTURES =====-----');
      console.error(err)
    };
  }

  async getFixtures() {
    await this.setMatchDay();
    await this.getEventsByMatchDay();
  }

  render() {
    return(
      <div className="container">
        <div className="row">
          <div className="section">
            <h3>{this.state.competition}</h3>
            <h5>Round {this.state.matchDay}</h5>
            {this.state.games.map((game, key) => (
              <div key={key} className="col s12 m4 center-align">
                <GameCard
                  home={game.homeTeamName}
                  homeScore={game.result.goalsHomeTeam || 0}
                  away={game.awayTeamName}
                  awayScore={game.result.goalsAwayTeam || 0}
                />
              </div>
            ))}
          </div>
        </div>
      </div>
    );
  }
}



