import React, { Component } from 'react';
import styled from 'styled-components';

const Card = styled.div`
  max-height: 130px;
  color: #333;
`;

const Team = styled.p`
  min-height: 3rem;
`;

const Score = styled.h3`
  font-size: 2rem;
  margin-top: -1rem;
`;

export default class GameCard extends Component {
  render() {
    return(
      <Card className="card">
        <div className="card-content center-align">
          <div className="row">
            {/* <span className="card-title">{this.props.date}</span> */}
            <div className="col s6 center-align">
              <Team>{this.props.home}</Team>
            </div>
            <div className="col s6 center-align">
              <Team>{this.props.away}</Team>
            </div>
          </div>
          <div className="row">
            <div className="col s6 center-align">
              <Score>{this.props.homeScore}</Score>
            </div>
            <div className="col s6 center-align">
              <Score>{this.props.awayScore}</Score>
            </div>
          </div>
        </div>
      </Card>
    );
  }
}



